.footer {
    width: 100%;

    &__container {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
    }

    &__section {
        padding: 4rem;

        a {
            color: #0070C1;
            &:hover {
                color: var(--blue);
            }
        }

        &--first{
            background-color: var(--lightGreen);
            color: var(--black);
            font-size: 1.5rem;
        }
        
        &--second{
            background-color: var(--green);
            color: var(--white);
            font-size: 1.8rem;
            small {
                font-weight: 400;
                display: block;
                width: 100%;
            }
        }
    }
}