@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
:root{--black: #454545;--blue: #0036c1;--green: #00B6C1;--lightGray: #BFBFC0;--lightGreen: #D0EEF0;--darkGray: #666666;--white: #FFFFFF}*{margin:0;padding:0;scroll-behavior:smooth;box-sizing:border-box}html{font-size:62.5%}.main__wrapper,.footer__wrapper{width:100%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center}.main__container,.footer__container{width:100%;max-width:120rem;margin:0 auto}.main__container p,.footer__container p{width:95%}body{font-family:'Roboto', Helvetica, Arial, sans-serif;color:#454545;color:var(--black);background-color:rgba(#BFBFC0, 0.25);background-color:rgba(var(--lightGray), 0.25);margin:0}img{max-width:100%;height:auto}.button{background-color:#DAE8FC;border-radius:.5rem;box-shadow:1px 1px 4px rgba(0,0,0,0.2);padding:1.5rem;align-self:flex-end}

.header__container{align-items:center;display:flex;flex-wrap:wrap;justify-content:space-between;margin:3rem 0;width:100%}.header__logo{align-items:flex-end;display:flex;margin-bottom:2rem}.header__logo--state img{margin-right:1.2rem}.header__city{font-size:1.8rem;font-weight:300;line-height:1.8rem}.header__span{font-size:1.2rem;font-weight:500;line-height:1.4rem;margin-right:1rem}

.home__container{display:flex;flex-direction:row;width:100%}.home__column{width:50%}.home__title{font-size:3.6rem;font-weight:700;line-height:4.2rem;margin-bottom:4rem}.home__text{font-size:2.2rem;font-weight:100;line-height:3rem;margin-bottom:1rem}.home__image{text-align:center}.home__image img{width:80%}.home__pills{display:flex;flex-direction:row;flex-wrap:wrap;margin:4rem 0}

.pills__container{align-items:center;background-color:var(--green);border-radius:2rem;color:var(--white);cursor:pointer;display:flex;font-size:1.7rem;font-weight:400;height:2.9rem;justify-content:center;margin-bottom:.5rem;margin-right:.5rem;padding:0 2rem;width:-webkit-max-content;width:max-content}.pills__container--active{background-color:var(--lightGray)}

.curator__container{align-items:center;background-color:rgba(0,182,193,0.5);border-radius:1rem;display:flex;flex-direction:row;justify-content:center;width:100%;margin-bottom:4rem;margin-top:4rem}.curator__photo{width:8.5rem;height:8.5rem;overflow:hidden;border-radius:50%;border:0.1rem solid var(--lightGray);margin:2rem 6rem}.curator__details{width:40%;display:flex;flex-direction:row;flex-wrap:wrap;align-items:center;margin:2rem 0}.curator__details div{display:flex;align-items:flex-end;margin-bottom:1rem}.curator__details div h3{font-size:2rem;line-height:2rem;margin:0}.curator__details div h6{color:var(--darkGray);font-size:1.2rem;line-height:1.3rem;margin:0 1rem;font-weight:400}.curator__details p{width:100%;font-size:1.5rem}

.footer{width:100%}.footer__container{display:flex;flex-direction:row;flex-wrap:nowrap}.footer__section{padding:4rem}.footer__section a{color:#0070C1}.footer__section a:hover{color:var(--blue)}.footer__section--first{background-color:var(--lightGreen);color:var(--black);font-size:1.5rem}.footer__section--second{background-color:var(--green);color:var(--white);font-size:1.8rem}.footer__section--second small{font-weight:400;display:block;width:100%}

.card__container{width:100%;max-width:22.2rem;color:var(--black)}.card__image{border:1px solid var(--lightGreen);border-radius:1rem;box-sizing:border-box;height:22.2rem;width:100%}.card__content{padding:1.6rem}.card__content h3{font-weight:700;font-size:2.4rem;line-height:2.8rem;color:var(--black)}.card__content p{font-size:1.8rem;line-height:2.1rem;margin-bottom:1rem}.card__facilities{display:grid;grid-template-columns:repeat(5, 3rem);grid-gap:.5rem;margin:1rem auto;list-style-type:none}.card__facilities__item{width:3rem;height:3rem;border-radius:.5rem;background-color:var(--blue)}.card__facilities__item--unchecked{background-color:var(--lightGray)}

.button{background-color:var(--green);border:1px solid #02939C;border-radius:.5rem;box-sizing:border-box;box-shadow:1px 1px 4px rgba(0,0,0,0.2);color:var(--white);cursor:pointer;font-size:1.4rem;padding:.7rem 1 rem}

.places{width:100%}.places__header{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:space-between;width:100%;margin:0 0 4rem}.places__title{font-size:3.2rem;color:var(--green)}.places__title span{color:var(--black)}.places__select{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:space-between;width:25rem;align-items:center;border-bottom:1px solid #000000;padding:.4rem 1.5rem}.places__select h5{color:#706D6D;font-size:1.8rem;font-weight:500}.places__select select{border:0 none;font-weight:500;font-size:1.8rem;color:var(--black);line-height:2.1rem;padding:0 .5rem}

