.home {
    &__container {
        display: flex;
        flex-direction: row;
        width: 100%;
    }
    &__column {
        width: 50%;
    }
    &__title {
        font-size: 3.6rem;
        font-weight: 700;
        line-height: 4.2rem;
        margin-bottom: 4rem;
    }
    &__text {
        font-size: 2.2rem;
        font-weight: 100;
        line-height: 3rem;
        margin-bottom: 1rem;
    }
    &__image {
        text-align: center;
        img {
            width: 80%;
        }
    }

    &__pills {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        margin: 4rem 0;
    }
}