.header {
  &__container {
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 3rem 0;
    width: 100%;
  }

  &__logo {
    align-items: flex-end;
    display: flex;
    // margin: 0 auto;
    margin-bottom: 2rem;

    &--state {
      
      img {
        margin-right: 1.2rem;
      }
    }

    &--ebac {
    }
  }

  &__city {
    font-size: 1.8rem;
    font-weight: 300;
    line-height: 1.8rem;
  }

  &__span {
    font-size: 1.2rem;
    font-weight: 500;
    line-height: 1.4rem;
    margin-right: 1rem;
  }
}
